import React from 'react';
import { Grid, Paper } from '@mui/material';
import CiAssignedLoansDataTable from '../../datatable/Loans/CiAssignedLoansDataTable';

export default function Dashboard(props) {

    return (
        <div style={{ width: '100%', height: '100%' }}>
            <Grid container>
                <Grid item xs={12} sx={{ p: 1, pt: 1 }}>
                    <Paper square elevation={1} sx={{ m: 0, p: 1, mt: 1 }}>
                        <CiAssignedLoansDataTable />
                    </Paper>
                </Grid>
            </Grid>
        </div>
    )
}
