import React from 'react';
import InnerDialog from '../shared/Dialog/InnerModalDialog';
import TransactionDateRange from '../forms/fields/TransactionDateRange';
import { Box, Button } from '@mui/material';

export default function DateRangeModal(props) {

    const [dateStart, setDateStart] = React.useState(props.dateStart ?? new Date());
    const [dateEnd, setDateEnd] = React.useState(props.dateEnd ?? new Date());

    const handleClose = () => {

        if (props.toggle) {
            props.toggle();
        }

    }

    const handleApply = () => {
        if(props.handleApply){
            
            const selectedDate = {
                startDate: dateStart,
                endDate: dateEnd
            };

            props.handleApply(selectedDate);
        }
    }

    return (
        <div>
            <InnerDialog open={props.open} title="Select Dates" toggle={props.toggle}>
                <Box sx={{ m: 1, ml: 0, mt: 2, minHeight: 380 }}>
                    <TransactionDateRange handleClose={handleClose} dateStart={dateStart} dateEnd={dateEnd} setDateStart={setDateStart} setDateEnd={setDateEnd} />
                    <Box display="flex" flexDirection="row" justifyContent="flex-end">
                        <Button variant="contained" sx={{ minWidth: 100 }} onClick={handleApply} >Apply</Button>
                        <Button variant="contained" color="secondary" sx={{ minWidth: 100, ml: 1 }} onClick={handleClose}>Cancel</Button>
                    </Box>
                </Box>
            </InnerDialog>
        </div>
    )
}
