import React from 'react'
import CollateralVerificationPage from '../pages/ci/CollateralVerificationPage';
import LoanDetailsDialog from '../shared/Dialog/LoanDetailsDialog';

export default function CollateralVerificationModal(props) {
    return (
        <div>
            <LoanDetailsDialog open={props.open} toggle={props.toggle} title="Verify Collaterals">
                <CollateralVerificationPage toggle={props.toggle} />
            </LoanDetailsDialog>
        </div>
    )
}
