import * as React from 'react';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

import { DateRangePicker } from 'react-date-range';

export default function TransactionDateRange(props) {

    const [pickerState, setPickerState] = React.useState([{
        startDate: props.dateStart ?? new Date(),
        endDate: props.dateEnd ?? new Date(),
        key: 'selection',
    }]);

    const handleSelect = (ranges) => {

        const { selection } = ranges;

        if (props.setDateStart) {
            props.setDateStart(selection.startDate);
        }

        if (props.setDateEnd) {
            props.setDateEnd(selection.endDate);
        }


        setPickerState([selection]);
    }

    return (
        <DateRangePicker
            ranges={pickerState}
            onChange={handleSelect}
        />
    )
}
