import React from 'react';
import config from '../../../../config/config';
import toastService from '../../../../services/toast/toastService';
import collateralsRepository from '../../../../repositories/collateralsRepository';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { get } from '../../../../redux/async/collateralsAsync';
import { getCiAssignedLoans } from '../../../../redux/async/loansAsync';
// MUI Components
import { Box, CircularProgress, Grid, LinearProgress, Link, Paper } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { makeStyles } from '@mui/styles';
import { FaRegCheckCircle, FaTimes } from 'react-icons/fa';

const useStyles = makeStyles({
    rowsTitleFirst: {
        border: '1px solid #000',
        fontWeight: 'bolder',
        textAlign: 'center',
    },
    rowsContentFirst: {
        border: '1px solid #000',
        padding: '5px',
        paddingLeft: '15px'
    },
    rowsTitle: {
        border: '1px solid #000',
        borderLeft: 'none',
        fontWeight: 'bolder',
        textAlign: 'center',
    },
    rowsContent: {
        border: '1px solid #000',
        borderLeft: 'none',
        borderTop: 'none',
        padding: '5px',
        paddingLeft: '15px',
        textAlign: 'center',

    },
})

export default function CollateralVerificationPage(props) {

    const classes = useStyles(props);

    const dispatch = useDispatch();

    const loanRedux = useSelector((state) => state.loans.value);
    const collateralRedux = useSelector((state) => state.collaterals.value);
    const collaterals = collateralRedux?.collaterals;
    const selectedLoan = loanRedux.selectedLoan;

    const [selectedCollateral, setSelectedCollateral] = React.useState("");
    const [loading, setLoading] = React.useState(false);

    const handleStatus = async (collateral, status, index) => {

        try {
            setSelectedCollateral(index);
            setLoading(true);

            let data = {
                collateral_id: collateral?.id,
                status: status,
                loan_code: selectedLoan?.loan_code
            }

            let result = await collateralsRepository.changeStatus(data);

            if (!result.error) {

                toastService.success(result.message);
                loadLoanCollaterals();
                dispatch(getCiAssignedLoans());
            } else {
                toastService.error(result.message);
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
        }
    }

    const loadLoanCollaterals = React.useCallback(async () => {
        await dispatch(get({ loan_code: selectedLoan?.loan_code })).unwrap();
    }, [dispatch])

    React.useEffect(() => {
        loadLoanCollaterals();
    }, [loadLoanCollaterals]);

    return (
        <div>
            <Paper sx={{ p: 1 }}>
                <Grid container>
                    <Grid item xs={3} className={classes.rowsTitleFirst}>
                        Collateral
                    </Grid>
                    <Grid item xs={3} className={classes.rowsTitle}>
                        Attachments
                    </Grid>
                    <Grid item xs={3} className={classes.rowsTitle}>
                        Status
                    </Grid>
                    <Grid item xs={3} className={classes.rowsTitle}>
                        Actions
                    </Grid>
                    {
                        collaterals.map((data, index) => {
                            return (
                                <React.Fragment key={`comaker-${index}`}>
                                    <Grid item xs={3} className={classes.rowsContent} sx={{ borderLeft: "1px solid #000 !important", textAlign: "left !important" }}>
                                        {
                                            data.collateral
                                        }
                                    </Grid>
                                    <Grid item xs={3} className={classes.rowsContent}>
                                        <Link key={`link-${index}`} href={`${config.DOMAIN}users/${data?.file_path}/${data?.file_name}`} target="_blank">
                                            Preview Attachment
                                        </Link>
                                    </Grid>
                                    <Grid item xs={3} className={classes.rowsContent}>
                                        {
                                            data.status === "1" ? "Verified" : "Unverified"
                                        }
                                    </Grid>
                                    <Grid item xs={3} className={classes.rowsContent}>
                                        {
                                            data.status === "1" ?
                                                <LoadingButton loading={loading && selectedCollateral === index} size="small" variant="contained" color="secondary" onClick={() => handleStatus(data, false, index)}>
                                                    Cancel
                                                    <FaTimes style={{ marginLeft: 3 }} />
                                                </LoadingButton>
                                                :
                                                <LoadingButton loading={loading && selectedCollateral === index} size="small" variant="contained" onClick={() => handleStatus(data, true, index)}>
                                                    Verify
                                                    <FaRegCheckCircle style={{ marginLeft: 3 }} />
                                                </LoadingButton>
                                        }
                                    </Grid>
                                </React.Fragment>
                            )
                        })
                    }
                </Grid>
                {collateralRedux.loading && <LinearProgress />}
            </Paper>
        </div>
    )
}
