import React from 'react';
import LoanCiRemarksForm from '../forms/LoanCiRemarksForm';
import LargeModalDialog from '../shared/Dialog/LargeModalDialog';

export default function LoanCiRemarksModal(props) {
    return (
        <div style={{ width: 100 }}>
            <LargeModalDialog open={props.open} toggle={props.toggle} title="Recommendations" size="sm">
                <LoanCiRemarksForm toggle={props.toggle} />
            </LargeModalDialog>
        </div>
    )
}
