import React from 'react';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';
import { BiChevronDown } from 'react-icons/bi'

function SearchLoans(props) {

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSelect = (value) => { 
        if(props.setLabel){
            props.setLabel(value);
        }
        handleClose();
    }

    return (
        <div>
            <IconButton
                color="default"
                disableRipple={true}
                size="small"
                onClick={handleClick}
                sx={{
                    minWidth: 50,
                    pr: 0,
                    backgroundColor: "transparent",
                    "&:hover": { backgroundColor: "transparent" }                    
                }}
            >
                <Typography sx={{ mr: 0.5 }}></Typography><BiChevronDown />
            </IconButton>
            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                    'aria-labelledby': 'basic-button',
                }}
            >
                <MenuItem onClick={() => handleSelect("code")}>By Loan Code</MenuItem>
                <MenuItem onClick={() => handleSelect("name")}>By Name</MenuItem>
            </Menu>
        </div>
    );
}

export default SearchLoans;