import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import toastService from '../../../services/toast/toastService';
import loanRepository from '../../../repositories/loanRepository';
import { TextField, Box } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { getCiAssignedLoans } from '../../../redux/async/loansAsync';

export default function LoanCiRemarksForm(props) {

    const dispatch = useDispatch();
    const loansRedux = useSelector((state) => state.loans.value);
    const selectedLoan = loansRedux?.selectedLoan;

    const [loading, setLoading] = React.useState(false);
    const [remarks, setRemarks] = React.useState("");

    const handleSubmit = async () => {
        try {

            setLoading(true);

            if (remarks === "") {
                toastService.modalInfo("Please set your remarks");
                return false;
            }

            let data = {
                loan_code: selectedLoan?.loan_code,
                remarks: remarks
            }

            let result = await loanRepository.ciRemarks(data);

            if (!result.error) {
                dispatch(getCiAssignedLoans());
                toastService.success(result.message);
                props.toggle();
            } else {
                toastService.error(result.message);
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
        }
    }

    return (
        <div>
            <Box display="flex" flexDirection="column">
                <TextField
                    value={remarks}
                    onChange={(e) => setRemarks(e.target.value)}
                    multiline
                    maxRows={3}
                    minRows={3}
                    placeholder="Write your recommendations here"
                    fullWidth
                />
                <Box display="flex" flexDirection="row" justifyContent='flex-end' sx={{ mt: 2 }}>
                    <LoadingButton loading={loading} onClick={handleSubmit} variant="contained" color="primary" sx={{ minWidth: 100 }}>
                        Submit
                    </LoadingButton>
                    <LoadingButton loading={loading} onClick={props.toggle} variant="contained" color="secondary" sx={{ ml: 1, minWidth: 100 }}>
                        Cancel
                    </LoadingButton>
                </Box>
            </Box>
        </div>
    )
}
