import React from 'react';
import { format, parseISO } from 'date-fns';
// Utilities 
import toastService from '../../../../services/toast/toastService';
import utilityService from '../../../../services/utils/utilityServices';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { getCiAssignedLoans as get, search } from '../../../../redux/async/loansAsync';
import { setSelectedLoan, setEndDate, setLoanStatus, setStartDate } from '../../../../redux/slices/loansSlice';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { Box, TextField, IconButton, Typography, Button, Tooltip } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// Page Components
import GridLoadingOverlay from '../../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../../grid/GridNoRowOverlay';
import Toolbar from '../../grid/Toolbar';
import DateRangeModal from '../../modals/DateRangeModal';
import LoanDetailsModal from '../../modals/LoanDetailsModal';
import CollateralVerificationModal from '../../modals/CollateralVeficationModal';
import SearchLoans from '../../forms/fields/SearchLoans';

// react-icons
import { FaSlidersH, FaSearch, FaCheck, FaFileContract, FaRedo, FaRegEdit } from 'react-icons/fa';
import { MdEditCalendar } from 'react-icons/md';
import LoanCiRemarksModal from '../../modals/LoanCiRemarksModal';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}


export default function CiAssignedLoansDataTable(props) {

    const dispatch = useDispatch();
    const loansRedux = useSelector((state) => state.loans.value);
    const masterfiles = useSelector((state) => state.masterfiles.value).masterfiles;
    const loading = loansRedux.loading;

    const startDate = loansRedux?.startDate;
    const endDate = loansRedux?.endDate;

    const [searchBy, setSearchBy] = React.useState("Member");
    const [keyword, setKeyword] = React.useState("");
    const [isSearch, setIsSearch] = React.useState(false);
    const [selected, setSelected] = React.useState([]);
    const [dateRange, setDateRange] = React.useState("");
    const [dateRangeModal, setDateRangeModal] = React.useState(false);
    const toggleDateRangeModal = () => setDateRangeModal(!dateRangeModal);
    const [loanDetailsModal, setLoanDetailsModal] = React.useState(false);
    const toggleShowLoanDetailsModal = () => setLoanDetailsModal(!loanDetailsModal);
    const [collateralVerifyModal, setCollateralVerifyModal] = React.useState(false);
    const togglesCollateralVerifyModalModal = () => setCollateralVerifyModal(!collateralVerifyModal);
    const [ciRemarksModals, setCiRemarksModal] = React.useState(false);
    const toggleCiRemarksModal = () => setCiRemarksModal(!ciRemarksModals);

    const width = 170;

    const actions = [
        {
            label: "Review Loan",
            icon: <FaFileContract size={18} />,
            event: (data) => handleSelectedLoan(data),
        },
        {
            label: "Verify Collaterals",
            icon: <FaCheck size={18} />,
            event: (data) => handleVerification(data),
        },
        {
            label: "Recommendations",
            icon: <FaRegEdit size={18} />,
            event: (data) => handleRecommendations(data)
        },
    ]

    const columns = [
        {
            field: 'applicant', headerName: 'Member', width: 250, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.applicant === null ? "..." : params.row.applicant}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'loan_code', headerName: 'Loan Code', width: 250, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.loan_code === null ? "..." : params.row.loan_code}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'loan_type_id', headerName: 'Loan Type', headerAlign: 'center', align: 'center', width: 100, renderCell: (params) => {
                let type = "";
                let loan_type = masterfiles?.loanTypes?.filter(data => {
                    return data.id === params.row.loan_type_id;
                });

                if (loan_type.length > 0) {
                    type = loan_type[0]?.type;
                }

                return (
                    <div>
                        <span>
                            {type === "" ? "..." : type}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'status', headerName: 'Status', headerAlign: 'center', align: 'center', width: 140, renderCell: (params) => {
                let status = 'Pending';
                let color = "#FFA117";
                if (params.row.status === "1") {
                    status = "Processing";
                    color = "#00BFA5";
                } else if (params.row.status === "2") {
                    status = "Approved";
                    color = "#00BFA5";
                } else if (params.row.status === '-1') {
                    status = "Rejected";
                    color = "#E45570";
                } else if (params.row.status === '3') {
                    status = "Released";
                    color = "#038AFF";
                } else if (params.row.is_completed === true) {
                    status = "Completed";
                    color = "#00BFA5";
                }

                return (
                    <div style={{
                        borderWidth: 2,
                        borderStyle: 'solid',
                        borderRadius: 7,
                        borderColor: color,
                        backgroundColor: color,
                        color: "#FFFFFF",
                        height: 25,
                        width: '70%',
                        textAlign: 'center',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        {status}
                    </div>
                );
            }
        },
        {
            field: 'date_applied', headerName: 'Date Applied', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.date_applied === null ? "..." : format(parseISO(params.row.date_applied), "MMMM dd, yyyy")}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'amount_applied', headerName: 'Amount Applied', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.amount_applied === null ? "..." : parseFloat(params.row.amount_applied).toFixed(2)}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'payment_length', headerName: 'Length of Payment', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.payment_length === null ? "..." : params.row.payment_length}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'interest_rate', headerName: 'Interest Rate', hide: true, width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.interest_rate === null ? "..." : params.row.interest_rate}%
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', flex: true, type: 'actions', getActions: (params) => {
                return actions.map((data, index) => {
                    return <GridActionsCellItem
                        key={`gridCell-${index}`}
                        icon={data.icon}
                        onClick={() => data.event(params.row)}
                        label={data.label}
                        sx={{
                            '&:hover': {
                                color: data.color ?? "#00BFA5"
                            }
                        }}
                        showInMenu
                    />
                });
            }
        },
    ];

    const handleSelection = (value) => {
        setSelected(value);
    }

    const handleVerification = (data) => {
        dispatch(setSelectedLoan(data));
        togglesCollateralVerifyModalModal();
    }

    const handleRecommendations = (data) => {
        dispatch(setSelectedLoan(data));
        toggleCiRemarksModal();
    }

    const handleSelectedLoan = (data) => {
        dispatch(setSelectedLoan(data));
        toggleShowLoanDetailsModal();
    }

    const handleKeyword = (keyword) => {
        setKeyword(keyword);
        if (keyword === "") {
            loadLoans();
        }
    }

    const handleSearch = async () => {

        if (keyword === "") {
            return false;
        }

        try {
            setIsSearch(true);
            let data = {}

            if (searchBy.toLowerCase() === "code") {
                data = {
                    code: keyword,
                    status: "1"
                }
            } else {
                data = {
                    name: keyword,
                    status: "1"
                }
            }

            await dispatch(search(data)).unwrap();

        } catch (e) {
            toastService.alert(e.toString());
        } finally {
            setIsSearch(false);
        }
    };

    const handleApply = (data) => {

        let selectedDateRange = "";

        if (data !== null) {
            dispatch(setLoanStatus(null));
            selectedDateRange = `${format(data.startDate, 'MMM dd, yyyy')} to ${format(data.endDate, 'MMM dd, yyyy')}`;
            dispatch(setStartDate(format(data.startDate, 'MMM dd, yyyy')));
            dispatch(setEndDate(format(data.endDate, 'MMM dd, yyyy')));
        }

        setDateRange(selectedDateRange);
        toggleDateRangeModal();
    }

    const handleResetDate = () => {
        dispatch(setStartDate(""));
        dispatch(setEndDate(""));
    }

    const loadLoans = React.useCallback(() => {

        let data = {
            startDate: new Date(startDate),
            endDate: new Date(endDate)
        }

        dispatch(get(data));

    }, [dispatch, startDate, endDate])

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            let dateRange = '';
            if (startDate !== '') {
                dateRange = `${startDate} to ${endDate}`;
            }

            setDateRange(dateRange);
            loadLoans();
        }

        return () => {
            mounted = true;
        }
    }, [loadLoans, setDateRange, startDate, endDate]);

    return (
        <div style={{ maxHeight: 550, minHeight: 550, width: '100%' }}>
            {dateRangeModal && <DateRangeModal open={dateRangeModal} toggle={toggleDateRangeModal} handleApply={handleApply} />}
            {loanDetailsModal && <LoanDetailsModal open={loanDetailsModal} toggle={toggleShowLoanDetailsModal} />}
            {collateralVerifyModal && <CollateralVerificationModal open={collateralVerifyModal} toggle={togglesCollateralVerifyModalModal} />}
            {ciRemarksModals && <LoanCiRemarksModal open={ciRemarksModals} toggle={toggleCiRemarksModal} />}
            <Box display="flex" flextDirection="row" justifyContent="space-between">
                <Box display="flex" flextDirection="row" >
                    <TextField
                        value={dateRange}
                        sx={{ minWidth: 400 }}
                        size='small'
                        placeholder='Select Date'
                        InputProps={{
                            startAdornment: (
                                <IconButton
                                    disableFocusRipple
                                    disableRipple
                                    sx={{
                                        borderRadius: 0,
                                        borderTopLeftRadius: 5,
                                        borderBottomLeftRadius: 5,
                                        mt: 0,
                                        ml: -2,
                                        mr: 1,
                                        backgroundColor: "#EEE",
                                        color: "#000"
                                    }}
                                >
                                    <Typography>
                                        Date Applied:
                                    </Typography>
                                </IconButton>
                            ),
                            endAdornment: (
                                <IconButton disableFocusRipple disableRipple>
                                    <MdEditCalendar size={18} />
                                </IconButton>
                            )
                        }}
                        inputProps={{
                            readOnly: true,
                        }}
                        onClick={toggleDateRangeModal}
                    />
                    <Tooltip arrow placement='top' title="Click to reset date">
                        <Button
                            variant="contained"
                            size="small"
                            disableFocusRipple
                            disableRipple
                            disableTouchRipple
                            sx={{
                                minWidth: 15,
                                p: 0,
                                pl: 2,
                                pr: 2,
                                m: 0,
                                ml: 1,
                                mt: 0.2,
                                height: 35,
                            }}
                            onClick={handleResetDate}
                        >
                            <FaRedo style={{ marginRight: 5 }} size={12} />
                            Reset
                        </Button>
                    </Tooltip>
                </Box>
                <Box display="flex" flextDirection="row" >
                    <TextField
                        type="search"
                        variant="outlined"
                        size="small"
                        onChange={(e) => handleKeyword(e.target.value)}
                        placeholder={`Search ${utilityService.capitalizeFirstLetter(searchBy)}`}
                        InputProps={{
                            endAdornment: <SearchLoans label={searchBy} setLabel={setSearchBy} />
                        }}
                        sx={{
                            mb: 1,
                            minWidth: 400
                        }}
                    />
                    <LoadingButton type="button" loading={isSearch} onClick={handleSearch} variant="contained" size="small" sx={{ height: 38, ml: 1, minWidth: 100 }}>
                        Search <FaSearch style={{ marginLeft: 7 }} />
                    </LoadingButton>
                </Box>
            </Box>
            <div style={{ height: 450 }}>
                <DataGrid
                    rows={loansRedux.loans}
                    columns={columns}
                    selectionModel={selected}
                    onRowDoubleClick={(params) => handleSelectedLoan(params.row)}
                    onSelectionModelChange={handleSelection}
                    loading={loading}
                    density="standard"
                    disableSelectionOnClick
                    disableColumnMenu
                    components={{
                        LoadingOverlay: LoadingOverlayOverride,
                        NoRowsOverlay: NoRowOverlayOverride,
                        MoreActionsIcon: ActionsIcon,
                        Toolbar: ToolbarOverride
                    }}
                />
            </div>

        </div>
    );
}